.graphTop{
    width: 100%;
    max-width: 1200px;
}
.graphTop .currencyChange{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.graphTop .currencyChange .currencyChangeDate{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #272C33;
    padding: 5px 10px;
    width: 400px;
    border-radius: 5px;
}

select{
    width: 100px;
    padding: 5px 0px;
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 5px;
    box-shadow: none;
    background-color: rgba(30,136,229,1);
    color:#fff;
}

.graphTop .cryptoChart{
    width: 100%;
    margin-top: 25px;
    background-color: #272C33;
    border-radius: 5px;
    padding: 15px;
}
.graphTop .cryptoChart .cryptoSelectedName{
    display: flex;
    align-items: center;
}
.graphTop .cryptoChart .cryptoSelectedName img{
    width: 35px;
    height: 35px;
    margin-right: 15px;
}

.graphTop .cryptoChart .chartJs .chartHeader{
    display: flex;
    justify-content: space-between;
    margin:15px 0px;
    align-items: center;
}

.graphTop .cryptoChart .chartJs .Chart{
    width: 100%;
    height: 400px;
}
.graphTop .cryptoChart .chartJs .chartBtn{
    display: flex;
    align-items: center;
    justify-content: center;
    margin:25px;
}

.graphTop .cryptoChart .chartJs .chartBtn button{
    padding: 10px 35px;
    margin: 0px 10px;
    border-radius: 5px;
    color: #fff;
    background-color: rgba(30,136,229,1);
    outline: none;
    border: none;
    cursor: pointer;
    box-shadow: none;    
}
