.nftTablinks{
    margin: 0px 15px;
    cursor: pointer;
    padding: 8px 15px; 
    
}
.active-nfttabs{
    border-bottom: 2px solid rgba(30,136,229,1);
    color: rgba(30,136,229,1);
}

.contentNft{
    display: none;
    transition: 0.4s;
}
.activeNft-content{
    display: block;
    transition: 0.4s;
}
.contentNft .addNft{
    padding: 10px 45px;
    border-radius: 5px;
    border: none;
    outline: none;
    box-shadow: none;
    background-color: rgba(30,136,229,1);
    color: #fff;
    cursor: pointer;
}
.addNftForm{
    position: fixed;
    inset: 0;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    align-items: center;
   
}

.addNftForm form{
    border-radius: 5px;
    width: 400px;
    background-color: rgba(30,136,229,1);
    padding:15px 25px;
    position: relative;
    height: 300px;
}
.addNftForm form img{
    position: absolute;
    padding: 5px;
    background-color: #272C33;
    border-radius: 50%;
    top: 5px;
    right: 5px;
    cursor: pointer;
}
.addNftForm form h3{
    margin-bottom: 15px;
}
.addNftForm form input{
    width: 100%;
    padding: 10px 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    outline: none;
    border: none;
    box-shadow: none;
}
.addNftForm form input[type="submit"]{
    cursor: pointer;
}
