
.tablinks{
  width: 100%;
  padding:6px 0px;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
}
.tablinks span{
  width: 25px;
  height: 25px;
margin-left: 5px;
}
.tablinks span img{
  width: 25px;
  height: 25px; 
}
 .tablinks h2{
  font-size: 17px;
  margin-left:10px ;
}
.active-tabs{
      background-color: rgba(30,136,229,0.7);
}

.moreOption a{
    display: block;
    text-decoration: none;
    padding: 5px;
    background-color: rgba(30,136,229,0.5);
    color: #000;
    border-radius: 5px;
    text-align: center;

}
.tablinks:hover{
  background-color: rgba(30,136,229,0.7);
}
.moreOption{
  position: absolute;
  width: 91%;
  border-radius: 5px 5px 0px 0px;
  padding: 10px;
  background-color:  #fff;
  bottom: 52px;
  color: #000;
  height: auto;
  z-index: 10;
  transition: 0.4s;
}
.moreOption a{
  display: block;
  text-decoration: none;
  padding: 5px;
  background-color: rgba(30,136,229,0.5);
  color: #000;
  border-radius: 5px;
  text-align: center;

}
.moreOption button{
  display: block;
  width: 100%;
  border: none;
  margin-top: 5px;
  padding: 8px;
  background-color: rgba(30,136,229,0.5);
  color: #000;
  cursor: pointer;
  border-radius: 5px;
}
.moreOption button.active-tabs{
  background-color: rgba(30,136,229,1);
}
.moreOption button:hover{
  background-color: rgba(30,136,229,0.7);
}
.content {
  transition: 0.4s;
  display: none;
}
.active-content {
  display: block;
}
