.referralTop{
    width: 100%;
    max-width: 1200px;
    background-color: #272C33;
    border-radius: 5px;
    padding: 15px;
    margin-top: 40px;
    height: 300px;
    position: relative;
}
.referralTop h3{
    position: absolute;
    bottom: 20px;
    color:#ccc;
}
.referralTop .row{
    display: flex;
    justify-content: space-between;

}
.referralTop .row .left,
.referralTop .row .right{
    width: 50%;
}
.referralTop .row .left h5,
.referralTop .row .right h5{
    margin-bottom: 15px;
    font-size: 25px;
    color:  rgba(30,136,229,1);
}
.referralTop .row .left span,
.referralTop .row .right span{
    display: flex;
    align-items: center;
}
.referralTop .row .left span input{
    width:30px;
    height: 30px;
    margin-left: 120px;
    background-color: rgba(30,136,229,1);
}
.referralTop .row .right span input{
    padding: 6px 15px;
    border-radius: 5px;
    background-color: #383F48;
    color: #fff;
    outline: none;
    box-shadow: none;
    margin-right: 35px;
    width: 300px;
}
.referralTop .row .right span img{
    width:40px;
    height: 40px;
    cursor: pointer;
    
}

.referralTop .row .left p,
.referralTop .row .right p{
    font-size: 16px;
    font-weight: 600;
    margin-top: 15px;
    color: #ccc;
}