.manageTop{
    width: 100%;
    margin: auto;
    max-width: 1200px;
}
.manageTop .manageTopHeader{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 15px 0px;
}
.manageTop .manageTopHeader .nftAssets{
    display: flex;
}
.manageTop .manageTopHeader .nftAssets .nft{
    display: flex;
    background-color: #272C33;
    border-radius: 5px;   
}


.manageTop .manageMainContent{
    width: 100%;
    background-color: #272C33;
    padding: 15px;
    border-radius: 5px;
    /* height: 400px; */
}

.manageTop .manageTopHeader .nftAssets  .BSC{
    background-color: #272C33;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6px 15px;
    border-radius: 5px;
    margin-left: 15px;
    cursor: pointer;

}
.manageTop .manageTopHeader .nftAssets  .BSC:hover{
    background-color:rgba(30,136,229,1);
}
.manageTop .manageTopHeader .nftAssets  .BSC img{
    margin-right:100px;
    
}
.manageTop .manageTopHeader .nftAssets  .ETH{
    background-color: #272C33;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6px 15px;
    border-radius: 5px;
    margin-left: 15px;
    cursor: pointer;
    margin-right: 15px;
}
.manageTop .manageTopHeader .nftAssets  .ETH img{
    margin-right: 10px;
}
.manageTop .manageTopHeader .nftAssets  .ETH h3{
    margin-right:90px;
    
}
.manageTop .manageTopHeader .nftAssets  .ETH:hover{
    background-color:rgba(30,136,229,1);
}

.manageTop .manageTopHeader .right{
    display: flex;
}
.manageTop .manageTopHeader .right img{
    margin: 0px 5px;
    background-color: rgba(30,136,229,0.3);
    border-radius: 50%;
    padding: 4px;
}
.manageTop .manageTopHeader .right img:hover{
    background-color: rgba(30,136,229,1);
    cursor: pointer;
}
.manageTop  .manageMainContent .totalNetworthHeader {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.manageTop  .manageMainContent .totalNetworthHeader .totalNetworth{
    display: flex;
    width: 400px;
    background-color:#383F48 ;
    align-items: center;
    padding: 5px 10px;
    border-radius: 5px;
    margin-bottom: 15px;
    justify-content: space-between;
}
.manageTop  .manageMainContent .totalNetworthHeader .searchCrypto{
    display: flex;
    align-items: center;
}
.manageTop  .manageMainContent .totalNetworthHeader .searchCrypto div{
    display: flex;
    align-items: center;
    margin-right: 15px;
}
.manageTop  .manageMainContent .totalNetworthHeader .searchCrypto div img{
    background-color: rgba(30,136,229,0.3);
    border-radius: 50%;
    padding: 2px;
    margin-left: 10px;
}
.manageTop  .manageMainContent .totalNetworthHeader .searchCrypto div img:hover{
    background-color: rgba(30,136,229,1);
    cursor: pointer;
}
.manageTop  .manageMainContent .totalNetworthHeader .searchCrypto input{
    padding: 6px 10px;
    outline: none;
    border: none;
    box-shadow: none;
    border-radius: 5px;
    background-color: #383F48;
    color:#fff;
}

.manageTop  .manageMainContent .AssetTop{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.manageTop  .manageMainContent .AssetTop .token,
.manageTop  .manageMainContent .AssetTop .hrsChange,
.manageTop  .manageMainContent .AssetTop .balance,
.manageTop  .manageMainContent .AssetTop .usdbalance,
.manageTop  .manageMainContent .AssetTop .empty{
    width: 20%;
    margin-bottom: 15px;
}

.manageTop  .manageMainContent .assetCoin{
    display: flex;
    justify-content: space-between;
    margin: 35px 0px;
   
}
.manageTop  .manageMainContent .assetCoin .tokenName,
.manageTop  .manageMainContent .assetCoin .hrsChangePrice,
.manageTop  .manageMainContent .assetCoin .balanceCoin,
.manageTop  .manageMainContent .assetCoin .usdbalanceCoin,
.manageTop  .manageMainContent .assetCoin .emptyShare{
    width: 20%;
}
.manageTop  .manageMainContent .assetCoin .tokenName{
    display: flex;
    align-items: center;
}
.manageTop  .manageMainContent .assetCoin .tokenName img{
    width: 35px;
    height: 35px;
    margin-right: 15px;
}
.manageTop  .manageMainContent .assetCoin .hrsChangePrice{
    display: flex;
    cursor: pointer;
    margin-right: 5px;
    border-radius: 5px;
}
.manageTop  .manageMainContent .assetCoin .hrsChangePrice:hover{
    background-color: #383F48;
}
.manageTop  .manageMainContent .assetCoin .hrsChangePrice img{
   margin-right: 15px;
   margin-top: -20px;
 
}
.manageTop  .manageMainContent .assetCoin .hrsChangePrice h3{
    font-size: 13px;
}
.manageTop  .manageMainContent .assetCoin .hrsChangePrice p{
    font-size: 12px;
}
.manageTop  .manageMainContent .assetCoin .emptyShare{
    text-align: center;
}
.manageTop  .manageMainContent .assetCoin .emptyShare img{
    width: 30px;
    height: 30px;
    padding: 5px;
    background-color: rgba(30,136,229,0.3);
    border-radius: 50%;
    cursor: pointer;
    margin:0px 5px;
}
.manageTop  .manageMainContent .assetCoin .emptyShare img:hover{
    background-color: rgba(30,136,229,1);
}
.moveSwapBack{
    padding: 5px 35px;
    background-color: rgba(30,136,229,1);
    color: #fff;
    margin-bottom: 15px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    display: flex;
    align-items: center;
    font-size: 17px;
}
.moveSwapBack img{
    margin-right: 10px;
}


.manageTop .receiveFunds{
    position: fixed;
    inset: 0;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
}
.manageTop .receiveFunds div{
    width: 400px;
    background-color: rgba(30,136,229,1);
    padding: 25px;
    border-radius: 5px;
    position: relative;
}
.manageTop .receiveFunds div img{
    position: absolute;
    padding: 5px;
    background-color: #272C33;
    border-radius: 50%;
    top: 5px;
    right: 5px;
    cursor: pointer;
}
.manageTop .receiveFunds div h2{
    margin-bottom: 15px;
    text-align: center;
}
.manageTop .receiveFunds div p{
    text-align: center;
    margin-bottom: 10px;
}
.manageTop .receiveFunds div form input{
    width: 100%;
    margin-bottom: 15px;
    padding: 10px 15px;
    border-radius: 5px;
    outline: none;
    box-shadow: none;
    border: none;
}
.manageTop .receiveFunds div form input[type="submit"]{
    width: 150px;
    text-align: center;
    display: block;
    margin: auto;
    cursor: pointer;
}