.marketTop{
    width: 100%;
    max-width: 1200px;
    margin: auto;
}
.marketTop .marketHeader{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.marketTop .marketHeader .formSearch form{
    display: flex;
    align-items: center;
    background-color: #272C33;
    border-radius: 5px;
}
.marketTop .marketHeader .formSearch form input{
    padding: 10px 15px;
    background-color: #272C33;
    border:none;
    outline:none;
    border-radius: 5px 0px 0px 5px;
    box-shadow: none;
    color: #fff;
    font-size: 17px;
}
.marketTop .marketHeader .formSearch img{
    cursor: pointer;
    padding: 0px 5px;
}

.marketTop  .marketCoin{
    width: 100%;
    background-color: #272C33;
    margin-top: 15px;
    border-radius: 5px;
    padding: 15px 15px;
}
.marketTop .marketCoin .marketHeading{
    display: flex;
    justify-content: space-between;
}
.marketTop .marketCoin .marketHeading .token,
.marketTop .marketCoin .marketHeading .price,
.marketTop .marketCoin .marketHeading .changeHrs,
.marketTop .marketCoin .marketHeading .empty{
    display: flex;
    align-items: center;
    width: 25%;
 
}
.marketTop .marketCoin .marketHeading .token h3,
.marketTop .marketCoin .marketHeading .price h3,
.marketTop .marketCoin .marketHeading .changeHrs h3,
.marketTop .marketCoin .marketHeading .empty h3{
    display: flex;
    align-items: center;
}

.marketTop .marketCoin .cryptoName{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 15px;
    cursor: pointer;
    border-radius: 5px;
    padding: 5px;
}
.marketTop .marketCoin .cryptoName:hover{
    background-color: #383F48;

}
.marketTop .marketCoin .cryptoName .cryptoIcon img{
    width: 35px;
    height: 35px;
    margin-right: 20px;
}
.marketTop .marketCoin .cryptoName .cryptoIcon {
    display: flex;
    align-items: center;
}
.marketTop .marketCoin .cryptoName .star img{
    float: right;
}
.marketTop .marketCoin .cryptoName .star img:hover{
    color: yellow;
}
.marketTop .marketCoin .cryptoName .cryptoIcon,
.marketTop .marketCoin .cryptoName .cryptoPrice,
.marketTop .marketCoin .cryptoName .cryptoHrsChange,
.marketTop .marketCoin .cryptoName .star{
    width: 25%;
} 
.moveBack{
    padding: 5px 35px;
    background-color: rgba(30,136,229,1);
    color: #fff;
    margin-bottom: 15px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    display: flex;
    align-items: center;
    font-size: 17px;
}
.moveBack img{
    margin-right: 10px;
}