.homeTop .connect__btn{
    padding: 10px 45px;
    cursor: pointer;
    border-radius: 5px;
    border: none;
    background-color: rgba(30,136,229,1);
    color: #fff;
    margin-bottom: 55px;
}
.homeTop .row{
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
    margin: auto;
}
.homeTop .row .card{
    width: 24%;
    text-align: center;
    padding: 25px 0px;
    border-radius: 5px;
    background-color: #272C33;
    cursor: pointer;
}
.homeTop .row .card:hover{
    border: 2px solid rgba(30,136,229,1);
}
.homeTop .row .card h2{
    margin-bottom: 15px;
}
.homeTop .row .card p{
    line-height: 29px;
    margin-bottom: 20px;
}
.moveBack{
    padding: 5px 35px;
    background-color: rgba(30,136,229,1);
    color: #fff;
    margin-bottom: 15px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    display: flex;
    align-items: center;
    font-size: 17px;
}
.moveBack img{
    margin-right: 10px;
}