.main{
    display: flex;
    justify-content: space-between;
}
.main .tab{
    width: 230px;
    background-color: #272C33;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    padding: 15px 10px;
    color: #fff;
}
.main .tab .logo {
    width: 100%;
    margin: auto;
}
.main .tab .logo img{
    width: 170px;
    display: block;
    margin: auto;
    margin-bottom: 20px;
}

/* Connect with metamast here ============================== */
.main .tab .connect{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 5px;
    padding: 1px 10px;
    background-color: rgba(30,136,229,0.5);
    cursor:pointer;
    margin-bottom: 10px;
    
}
.main .tab .connect:hover{
    background-color: rgba(30,136,229,0.7);
}
.main .tab .connect .connect__Img{
    width: 25px;
    height: 25px;
}
.main .tab .connect h2{
    font-size: 17px;
}
.main .tab .connect .connect__share{
    width: 35px;
    height: 35px;
}



/* wallet section here ====================================  */
.main .tab .wallet{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
}

.main .tab .wallet h2{
    background-color: rgba(30,136,229,0.5);
    padding: 6px 25px;
    border-radius: 5px;
    font-size: 17px;
    cursor: pointer;
}
.main .tab .wallet h2:hover{
    background-color: rgba(30,136,229,0.7);
}
.main .tab .wallet img{
    background-color: rgba(30,136,229,0.5);
    width: 35px;
    height: 35px;
    border-radius: 5px; 
    cursor: pointer;
}
.main .tab .wallet img:hover{
    background-color: rgba(30,136,229,0.7);
}
.main .tab .more{
    position: absolute;
    bottom: 10px;
    background-color: rgba(30,136,229,0.5);
    display: flex;
    width: 91%;
    border-radius: 5px;
    padding:6px 0px;
   cursor: pointer;
}
.main .tab .more span{
    margin-left: 5px;
    width: 30px;
    
} 
.main .tab .more h2{
    font-size: 17px;
    margin-left:10px ;
}
.main .tab_content{
    width:100%;
    padding: 25px ;
    margin-left: 230px;
    color:#fff;
}

