.formTab{
    margin-right: 15px;
    padding: 10px 10px;
    cursor: pointer;
    margin-bottom: -2px;
}
.active-formTab{
    border-bottom: 2px solid rgba(30,136,229,1);
    color: rgba(30,136,229,1);
}
.formContent{
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 45px;
}
.formContent{
    display: none;
}
.active-form{
    display: block;
}
.active-form .formRow{
    display: flex;
    justify-content: space-between;
    margin: 15px 0px;
}
.active-form .formRow input{
    width: 48%;
    padding: 15px 15px;
    border-radius: 5px;
    border: none;
    outline: none;
    box-shadow: none;
    background-color: #383F48;
    color: #fff;
    font-size: 17px;
}
#email{
    padding: 15px 15px;
    width: 100%;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: #383F48;
    box-shadow: none;
    color: #fff;
    font-size: 17px;
}
#notes{
    padding: 15px 15px;
    width: 100%;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: #383F48;
    box-shadow: none;
    color: #fff;
    font-size: 17px;
}
.buy__btn{
    padding: 15px 45px;
    margin-top: 45px;
    background-color: rgba(30,136,229,1);
    color: #fff;
    outline: none;
    box-shadow: none;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
