.SwapTop{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 1200px;
}
.SwapTop .swapInner{
    width: 400px;
    max-width: 100%;
    padding: 15px;
    border-radius: 5px;
    background-color: #272C33;
    
    position: relative;
}
.SwapTop .swapInner .transferCrypto{
    position: absolute;
    left: -55%;
    padding: 10px 5px;
    border-radius: 5px;
    background-color: #272C33;
    width: 200px;
}
.SwapTop .swapInner .transferCrypto .seletCrypto{
    width: 100px;
    text-align: right;
    background-color: #383F48;
    padding: 2px ;
    margin: 10px 0px;
    border-radius: 5px;
    cursor: pointer;
}
.SwapTop .swapInner .transferCrypto h3{
    margin: 7px 0px;
    color: rgba(30,136,229,1);
}
.SwapTop .swapInner .transferCrypto input{
    padding: 5px;
    border-radius: 5px;
    outline: none;
    width: 100%;
    box-shadow: none;
    border: none;
    background-color: #383F48;
    color: #fff;
}
.SwapTop .swapInner .transferCrypto input[type='submit']{
    background-color: #383F48;
    cursor: pointer;
    padding: 5px 25px;
    margin-top: 15px;
}
.SwapTop .swapInner .headerTop{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.SwapTop .swapInner .headerTop .connnecBtn{
    display: flex;
    justify-content: space-around;
    background-color: #383F48;
    width: 150px;
    padding: 5px 10px;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
}
.SwapTop .swapInner .headerLeft{
    display: flex;
    justify-content: space-between;
}
.SwapTop .swapInner .shuffle img{
   padding: 5px;
   border-radius: 50%;
   background-color: #383F48;
   cursor: pointer;
   margin: 0px 5px;
}

.SwapTop .swapInner .ChooseFrom{
    width: 100%;
    padding: 10px 5px;
    background-color: #383F48;
    margin-top: 10px;
    border-radius: 5px;
    position: relative;
}
.SwapTop .swapInner .ChooseFrom p{
    font-size: 13px;
    font-weight: bolder;
    color:#ccc;
}
.SwapTop .swapInner .ChooseFrom h2{
    margin-top: -5px;
    color: rgba(30,136,229,1);
    font-size: 20px;
}

.SwapTop .swapInner .ChooseFrom .chooseCoin{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 15px 0px;
  

}
.SwapTop .swapInner .ChooseFrom .chooseCoin .chooseCurrency{
    padding: 8px 10px;
    background-color: #272C33;
    width: 40%;
    cursor: pointer;
    border-radius: 5px;
}
.SwapTop .swapInner .ChooseFrom .chooseCoin .chooseCurrency img{
    float: right;
}
.SwapTop .swapInner .ChooseFrom .chooseCoin input{
    padding: 10px 10px;
    border-radius: 5px;
    background-color: #383F48;
    border: none;
    outline: none;
    box-shadow: none;
    color: #fff;
    font-size: 18px;
    width: 50%;
    text-align: right;
}
.SwapTop .swapInner .ChooseFrom .chooseCoin input[type="number"]::placeholder { 
    color: #fff;
    /* Firefox, Chrome, Opera */
    text-align: right;
}
.SwapTop .swapInner .ChooseFrom .fillDown{
    position: absolute;
    bottom: -20px;
    background-color: #fff;
    left: 45%;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: 2px solid rgba(30,136,229,1);
}
.SwapTop .swapInner .estimate{
    width: 100%;
    background-color: #383F48;
    margin-top: 10px;
    border-radius: 5px;
    padding: 15px 10px;
}
.SwapTop .swapInner .estimate h2{
    color: rgba(30,136,229,1);
    font-size: 20px;
}

.SwapTop .swapInner .estimate .row{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 15px 0px;
}
.SwapTop .swapInner .estimate .row .estimateCurrency{
    padding: 8px 10px;
    background-color: hsl(215, 13%, 18%);
    width: 40%;
    cursor: pointer;
    border-radius: 5px;
    position: relative;
    text-align: right;
}

.SwapTop .swapInner .estimate .row .estimatedCoin{
  font-size: 20px;
  font-weight: bolder;
  padding: 10px 0px;
}

.SwapTop .swapInner h2{
    font-size: 20px;
    margin-top: 25px;
}
.SwapTop .swapInner button{
    width: 100%;
    text-align: center;
    padding: 15px 0px;
    margin-top: 15px;
    border-radius: 5px;
    outline: none;
    box-shadow: none;
    border: 1px solid #383F48;
    cursor: pointer;
    background-color: #383F48;
    color: #fff;
    font-weight: bolder;
}
.SwapTop .swapInner button:hover{
    border: 1px solid rgba(30,136,229,1);
}
.SwapTop .shuffleBackdrop{
    position: fixed;
    inset: 0;
    background-color: rgba(0,0,0,0.3);
    display: flex;
    align-items: center;
    justify-content: center;
}
.SwapTop .shuffleInner{
    width: 400px;
    padding: 15px 10px;
    background-color: rgb(39, 44, 51,1);
    border-radius: 5px;
    border: 1px solid rgba(30,136,229,1);
}
.SwapTop .shuffleInner h2{
    margin-bottom: 5px;
    font-weight: bolder;
}
.SwapTop .shuffleInner p{
    color: #ccc;
}
.SwapTop .shuffleInner .switchImg{
 display: flex;
 text-align: center;
 margin: 20px 0px;
}
.SwapTop .shuffleInner .switchImg img{
    cursor: pointer;
   
}
.SwapTop .shuffleInner .switchImg .binace{
    margin-right: 35px;
    text-align: center;
}
.SwapTop .shuffleInner .switchImg .binace small{
    margin-top: -10px;
    color: #ccc;
    font-weight: bolder;
}
.SwapTop .shuffleInner .switchImg .binace img,
.SwapTop .shuffleInner .switchImg .ethereum img{
    width: 100px;
    height: 100px;
}